@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
:root{
  --color-01: rgb(4, 4, 39);
  --color-02: rgb(21, 23, 39);
  --color-03: rgb(34, 34, 34);
  --color-04: whitesmoke;
  --color-05: rgb(4, 0, 7);
  --color-06: gray;
  --color-07: rgb(50, 50, 94);
  --head_heigth: 60px;
  --main_heigth: calc(100vh - var(--head_heigth));
}
body {
  position: relative;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(34, 34, 34);
  background-color: var(--color-03); 
  background-image: url(https://cdn2.iconfinder.com/data/icons/art-and-crafting-set/128/iStar_Design_Art_LineIcons_Live-105-512.png);
  background-size: 40px;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.color{
  color: rgb(0, 0, 29);
  color: rgb(8, 10, 26);
  color: rgb(34, 34, 34);
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.container{
    width: 90%;
    margin: 0 auto;
    max-width: 800px;
}
*{
    box-sizing: border-box;
}
body{
    font-family: 'Roboto', sans-serif;
    color: var(--color-04);
}

.section__h_img{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.header__img{
    width: 30px;
}
.p_h_img{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-animation: miau 10s infinite;
            animation: miau 10s infinite;
    height: 16px;
    width: 40px;
}
@-webkit-keyframes miau{
    0% {font-size: 0px;}
    90%{font-size: 0px;}
    100% {font-size: 16px;}
}
@keyframes miau{
    0% {font-size: 0px;}
    90%{font-size: 0px;}
    100% {font-size: 16px;}
}
.p_logo, .p_hide{
    font-family: 'Akaya Telivigala', cursive;
    font-size: 20px;
}
.p_logo{
    color: var(--color-04);
}
.p_hide{
    color: var(--color-03);
}

.header{
    width: 100%;
    background-color: var(--color-01);
    height: var(--head_heigth);
}
.header-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.main,.main-reponse{
    background-color: var(--color-02);
    height: var(--main_heigth);
    display: grid;
    grid-template-columns: 100%;
    
}
.main{
    grid-template-rows: 1fr 60px;
}
.main-reponse{
    grid-template-rows: 1fr auto 60px;
}

.change{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgba(0, 7, 12, 0.288);
}
.change__ul{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 2px;
    gap: 2px;
    font-size: 25px;
}
.change__li{
    background-color: var(--color-07);
    border: 5px solid var(--color-07);
    cursor: pointer;
}
.change__li:hover{
    background-color: var(--color-01);
    border: 5px solid var(--color-01);
}
.inv{
    display: none;
}
.msg{
    position: relative;
    max-width: 60%;
    min-width: 200px;
    min-height: 50px;
    border-radius: 10px;
    background-color: var(--color-06);
    display: -webkit-flex;
    display: flex;
    display: table-cell;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    padding: 10px;
    overflow: hidden;
    word-wrap: break-word;
}
.msg-img{
    display: none;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 0;
    background-color: var(--color-06);
    transition: 1s;
}
.msg:hover > .msg-img{
    display: inline;
}
.msg-div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 5px;
    gap: 5px;
}
.msg-sect{
    background-color: var(--color-05);
    padding: 10px;
    border-radius: 5px;    
}
.msg__list{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    padding: 0 20px;
    padding-top: 5px;
    overflow-y: scroll;
}
.msg__li{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
:root{
    --form__heigth: 30px;
    --form__heigth__button: calc(1px + var(--form__heigth));
}
.form__msg{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 10px;
    width: 100%;
}
.form__txt{
    width: 79%;
    height: 30px;
    height: var(--form__heigth);
    resize: none;
}
.form__button{
    cursor: pointer;
    background-color: var(--color-06);
    border:none;
    width: 20%;
    height: calc(1px + 30px);
    height: var(--form__heigth__button);
    transition: 200ms;
}
.form__button:focus{
    outline: none;
    box-shadow: none;
}
.form__button:hover{
    opacity: .9;
}

.response-section{
    background-color: var(--color-05);
    padding: 0 10px;
    border: 10px solid var(--color-05);
    border-radius: 5px 5px 0 0;
    display: grid;
    grid-template-columns: 95% 10px;
    grid-gap: 5px;
    gap: 5px;
    grid-template-rows: auto;
}
.response-p{
    display: table-cell;
    word-wrap: break-word;
    width: 100%;
}
.responde-img{
    cursor: pointer;
    display:  -webkit-flex;
    display:  flex;
 
}
